.history-closing {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  &__text {
    font-size: 2rem;
    font-weight: bold;
    color: $fontColor02;
    line-height: 2.5;
    padding: 80px;

    @include media("sp") {
      font-size: 1.6rem;
      line-height: 3;
      padding: 30px;
    }
  }
  &__message {
    font-size: 2.7rem;
    font-weight: bold;
    color: $fontColor02;
    line-height: 2.5;

    @include media("sp") {
      font-size: 1.8rem;
    }
  }
  h2::before {
    content: '＼';
    padding-right: 20px;
  }
  h2::after {
    content: '／';
  }

  .sp-break {
    display: none;

    @include media("sp") {
    display: block;
    }
  }

}

.history-closing-picture {
  display: flex;
  flex-direction: column;
  align-items: center;
  &__picture {
    max-width: 100%;
  }

}
