.concept-introduction {
  text-align: center;

  &__text {
    font-size: 2.2rem;
    color: $fontColor03;
    line-height: 2;

    @include media("sp") {
      font-size: 1.8rem;
      line-height: 3;
    }

    .sp-break {
      display: none;

      @include media("sp"){
        display: block;
      }
    }
  }
  &__text-strong {
    font-size: 3.8rem;
    color: $fontColor02;
    font-weight: bold;
    line-height: 3;

    @include media("sp") {
      font-size: 2rem;
    }
  }

}
