.activity-lists {
  text-align: center;

  @include media ("sp") {
    display: none;
  }

  &-card {
    display: inline-block;
    width: 28%;
    border-radius: 16px;
    box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.1);
    margin: 0 1% 60px;

    &__picture img {
      border-radius: 5px;
      max-width: 100%;
    }
    &__text {
      color: $fontColor02;
      font-size: 2rem;
      font-weight: bold;
      line-height: 4.5
    }
  }

  &-card:hover {
    @include hover-moveUp;
  }
}
