nav {
  position: fixed;
  width: 100%;
  // height: 80px;
  background-color: $fontColor04;
  top: 0;
  left: 0;
  right: 0;
  opacity: 0;
  z-index: 2;
  box-shadow: 0px 1px 4px 2px rgba(0,0,0,0.15);
}

.global-navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 980px;
  height: 100%;
  margin: 0 auto;

  @include media("tb") {
    max-width: 100%;
  }

  &__logo img {
    height: 60px;
  }
  &__logo:hover {
    @include hover-opacity;
  }


  &__button {
    display: none;
    width: 60px;
    height: 60px;
    vertical-align: middle;
    cursor: pointer;
    position: fixed;
    top: 10px;
    right: 0;
    z-index: 100;
    background-color: $backgroundColor04;
    transform: translateZ(0);
    transition: all .5s;

    @include media("sp") {
      display: inline-block;
    }

    span {
      display: inline-block;
      box-sizing: border-box;
      position: absolute;
      left: 19px;
      width: 42%;
      height: 4px;
      background-color: $backgroundColor02;
      border-radius: 10px;
      transition: all .5s;
    }
  }
  &__button.active {
    transform: translate(-250px);
    transition: all .5s;
  }

  &__button.active span {
    background-color: #fff;
  }
  &__button span:nth-of-type(1) {
    top: 18px;
  }
  &__button.active span:nth-of-type(1) {
    transform: translateY(9px) rotate(-45deg);
  }
  &__button span:nth-of-type(2) {
    top: 27px;
  }
  &__button.active span:nth-of-type(2) {
    opacity: 0;
  }
  &__button span:nth-of-type(3) {
    bottom: 20px;
  }
  &__button.active span:nth-of-type(3) {
    transform: translateY(-9px) rotate(45deg);
  }

  &-menu {
    @include media("sp") {
      display: none;
    }
    ul {
      display: flex;
      justify-content: space-around;

      li {
        a {
          font-size: 1.2rem;
          color: $fontColor01;
          font-weight: bold;
          margin-left: 35px;

          @include media("tb") {
            margin-left: 25px;
          }
        }
        a:hover {
          @include hover-opacity;
        }
      }
    }
  }

  &-menu-sp {
    width: 250px;
    height: 100%;
    background-color: $backgroundColor02;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 10;
    transform: translate(250px);
    transition: all .5s;

    ul {
      padding-left: 0;

      li {
        padding: 20px 20px;

        a {
          font-size: 1.7rem;
          color: $fontColor01;
          font-weight: bold;
          margin-left: 15px;
        }
      }
    }
  }

  &-menu-sp.open {
    transform: translateZ(0);
  }

}
