.fadeInUp {
    opacity : 0;
    transform : translate(0, 50px);
    transition:all 1.0s;
}
.fadeInUp.scrollin {
    opacity : 1;
    transform : translate(0, 0);
}

.fadeInRight {
    opacity : 0;
    transform : translate(-50px, 0);
    transition:all 1.0s;
}
.fadeInRight.scrollin {
    opacity : 1;
    transform : translate(0, 0);
}

.fadeInLeft {
    opacity : 0;
    transform : translate(50px, 0);
    transition:all 1.0s;
}
.fadeInLeft.scrollin {
    opacity : 1;
    transform : translate(0, 0);
}

.bounce {
  transform: scale(0);
}
.bounce.scrollin {
  animation-name: letterBounce;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

.fadeInSequence {
  opacity : 0;
  transform : translate(0, 50px);
  transition:all 1.0s;
}
.fadeInSequence.scrollin{
  opacity : 1;
  transform : translate(0, 0);
}


@keyframes letterBounce {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 1;
    transform: scale(1.1);
  }
  50% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
  }
}
