@mixin header-text {
  font-size: 2rem;
  color: $fontColor02;
  font-weight: bold;
}

@mixin hover-opacity {
  opacity: 0.6;
}
@mixin hover-zoomup {
  transform: scale(1.1);
  transition-duration: 0.3s;
}
@mixin hover-moveUp {
  // position: relative;
  transform: translateY(-5px);
  transition-delay: 0;
  // transition-duration: 0.2s;
}
