html {
  font-size: 62.5%;
  margin: 0;
  padding: 0;
}
body {
  font-size: 1.2rem;
  font-family: $fontFamily01;
  margin: 0;
  padding: 0;
}
a:link, a:visited, a:hover, a:active {
  text-decoration: none;
}
ul li {
  list-style-type: none;
}
.container {
  padding: 0;
}
.top-image-wrapper {
  background: url(../img/header.jpg) no-repeat top center/cover;
  padding-top: 64%;
  position: relative;
}
.concept-wrapper {
  padding-bottom: 80px;

  @include media ("sp") {
    .container {
      padding-left: 12px;
      padding-right: 12px;
    }
  }

}
.history-wrapper {
}
.activity-list-wrapper {
}
.activity-details-wrapper {
  background-color: $backgroundColor01;
  padding: 80px 0;

  @include media ("sp") {
    padding: 30px 0;;
  }
}
.member-wrapper {
}
.news-wrapper {
  background-color: $backgroundColor01;
  padding-bottom: 100px;
  @include media("sp") {
    padding-bottom: 40px;
  }
}
.calender-wrapper {
  text-align: center;

  @include media ("sp") {
    display: none;
  }
}
.sponsor-wrapper {
  background-color: $backgroundColor01;

  @include media ("sp") {
  background-color: $backgroundColor02;
  }
}
.sns-wrapper {
  background: url(../img/footer.jpg) top center/cover;
  padding-top: 100px;
  padding-bottom: 43%;

  @include media ("sp") {
    padding-top: 30px;
    padding-bottom: 55%;
  }
}
.footer {
  background-color: $backgroundColor03;
}
