.activity-details {
  background-color: #ffffff;
  margin-top: 100px;
  margin-bottom: 100px;
  @include media("sp") {
    margin-top: 50px;
    margin-bottom: 50px;
  }

  &-card {
    display: flex;
    padding: 2%;

    @include media ("sp") {
      display: block;
    }

    &__picture {
      width: 49%;

      @include media ("sp") {
        width: 100%;
      }
    }
    &__picture img {
      position: relative;
      top: -50px;
      left: -50px;
      vertical-align: top;
      border-radius: 5px;
      max-width: 100%;
      box-shadow: 6px 6px 10px rgba(0, 0, 0, 0.1);

      @include media ("sp") {
        position: static;
      }
    }

    &__text {
      width: 49%;

      @include media ("sp") {
        width: 100%;
      }

      h3 {
        font-size: 3.6rem;
        font-weight: bold;
        line-height: 3;
        letter-spacing: 1.5px;
        padding-left: 10px;
        @include media("sp") {
          font-size: 2.4rem;
        }
      }
      p {
        font-size: 1.7rem;
        line-height: 1.8;
      }
      p + p {
        font-size: 1.7rem;
        font-weight: bold;
        @include media("sp") {
          font-size: 1.4rem;
        }
      }
      a {
        color: $fontColor05;
        border-bottom: solid 1px $fontColor05;
      }
      a:hover {
        @include hover-opacity;
      }
      img {
        width: 300px;
      }
    }

  }

  &-appendix {
    display: flex;
    justify-content: space-around;
    padding: 4%;

    @include media ("sp") {
      display: block;
    }

    &__link {
      width: 30%;
      padding: 2%;

      @include media ("sp") {
        display: block;
        margin: 0 auto;
        width: 90%;
      }
    }
    &__link img {
      max-width: 100%;
    }
    &__link:hover {
      @include hover-opacity;
    }
  }
}

.activity-details:first-of-type {
  margin-top: 0;
}
.activity-details:last-of-type {
  margin-bottom: 0;
}
