.history-details {
  width: 90%;
  margin: 0 auto;

  &-innerLeft {
    background: $backgroundColor02;
    display: flex;
    padding: 20px;

    @include media("sp") {
      flex-direction: column;
      align-items: center;
      padding-top: 0;
      padding-bottom: 10px;
    }

    &__picture {
      position: relative;
      left: -40px;
      top: -40px;
      border-radius: 5px;
      max-width: 50%;
      align-self: flex-start;

      @include media("sp") {
        position: static;
        max-width: 100%;
      }
    }
    &__text {
      font-size: 2rem;
      color: $fontColor03;
      line-height: 2;
      max-width: 50%;
      align-self: center;

      @include media("sp") {
        max-width: 100%;
        font-size: 1.6rem;
      }
    }
  }

  &-innerRight {
    background: $backgroundColor02;
    display: flex;
    padding: 20px;

    @include media("sp") {
      flex-direction: column;
      align-items: center;
      padding-top: 0;
      padding-bottom: 10px;
    }

    &__picture {
      order: 2;
      position: relative;
      right: -40px;
      top: -40px;
      border-radius: 5px;
      max-width: 50%;
      align-self: flex-start;

      @include media("sp") {
        order: 1;
        position: static;
        max-width: 100%;
          }
    }
    &__picture--position-static {
      order: 2;
      border-radius: 5px;
      max-width: 50%;
      position: static;
      align-self: center;

      @include media("sp") {
        max-width: 100%;
      }
    }
    &__text {
      font-size: 2rem;
      color: $fontColor03;
      line-height: 2;
      max-width: 50%;
      align-self: center;

      @include media("sp") {
        order: 2;
        max-width: 100%;
        font-size: 1.6rem;
      }
    }
  }

  &__png {
    display: block;
    margin: 0 auto;
    padding: 30px 0 70px 0;
    max-width: 100%;

    @include media("sp") {
      padding: 25px 0;
    }
  }

}
