.member-sub-box {
  padding: 50px 0;

  &-card {
    width: 33%;
    display: inline-block;
    vertical-align: top;
    text-align: center;
    padding: 30px;

    @include media ("sp") {
      display: block;
      width: 100%;
    }

    &__picture {

    }
    &__picture img {
      max-width: 100%;
    }

    &__text {

      p:first-of-type {
        line-height: 5;
      }
      p {
        font-size: 2rem;
        font-weight: bold;
        line-height: 2;
        max-width: 100%;
        margin: 0 auto;
        img {
          margin-left: 10px;
          vertical-align: middle;
          transform: scale(1);
          transition: 1s;
        }
        img:hover {
          @include hover-zoomup;
        }
      }
      p + p {
        font-size: 1.5rem;
        font-weight: normal;
      }
      p + a {
        color: $fontColor05;
        font-size: 1.2rem;
        letter-spacing: 1.2;
        line-height: 2;
        border-bottom: solid 1px $fontColor05;
      }
      p + a:hover {
        @include hover-opacity;
      }
    }

  }


}
