.footer-menu {
  display: flex;
  justify-content: space-between;

  @include media ("sp") {
    display: block;
  }

  &-left {
    width: 60%;

    @include media ("sp") {
      width: 100%;
    }

    &-sns-logo {
    @include media ("sp") {
      display: flex;
      justify-content: center;
    }

      a + a {
        margin-left: 30px;
      }
      img {
        transform: scale(1);
        transition: 1s;
      }
      img:hover {
        @include hover-zoomup;
      }

    }

    &-nav {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      padding: 40px 0 0 0;

      @include media ("sp") {
        display: none;
      }

      li {
        a {
          color: $fontColor04;
          font-size: 1.3rem;
        }
        a:first-of-type {
          padding-left: 0;
        }
        a:hover {
          @include hover-opacity;
        }
      }
    }

  }

  &-right {
    width: 30%;

    @include media ("sp") {
      display: none;
    }
  }

}
